<template>
  <v-menu offset-y>
    <template v-slot:activator="{ props }">
      <v-btn text v-bind="props" :class="buttonClass" :style="buttonStyle">
        <v-icon left>mdi-television-play</v-icon>
        {{ currentEpisodeLabel }}
        <v-icon right>mdi-chevron-down</v-icon>
      </v-btn>
    </template>
    <v-list>
      <v-list-item
        v-for="episode in episodes"
        :key="episode.id"
        @click="selectEpisode(episode)"
      >
        <v-list-item-title>{{ episode.title }}</v-list-item-title>
      </v-list-item>
    </v-list>
  </v-menu>
</template>

<script>
export default {
  name: 'EpisodeSelector',
  props: {
    episodes: {
      type: Array,
      required: true,
    },
    currentEpisode: {
      type: String,
      // Can be null initially, so not required
      default: null,
    },
    buttonClass: {
      type: String,
      default: ''
    },
    buttonStyle: {
      type: Object,
      default: () => ({})
    }
  },
  computed: {
    currentEpisodeLabel() {
      if (!this.currentEpisode) return 'Select Episode';
      const episode = this.episodes.find(e => e.value === this.currentEpisode);
      return episode ? episode.title : 'Select Episode';
    },
  },
  methods: {
    selectEpisode(episode) {
      this.$emit('episode-changed', episode);
    },
  },
};
</script>
