<script>
import { baseColors, colorVariants } from '../utils/vuetifyColorNames';
import { resolveVuetifyColor, updateColor, getColorValue } from '../utils/themeColorMap';

export default {
  data() {
    return {
      rundownTypes: ['Advert', 'CTA', 'Promo', 'Segment', 'Trans'],
      interfaceTypes: ['Selection', 'Hover', 'Highlight', 'Dropline', 'DragLight'],
      scriptStatusTypes: ['Draft', 'Approved', 'Production', 'Completed'],
      typeColors: {},
      showConfirmation: false,
      isSaving: false,
      baseColor: {},
      variant: {},
    }
  },
  computed: {
    baseColorOptions() {
      if (!Array.isArray(baseColors)) {
        console.warn('[ColorSelector] baseColors is not an array:', baseColors);
        return [];
      }
      // Convert baseColors to array of { title, value }
      return baseColors.map(c => ({ title: c, value: c }));
    },
    variantOptions() {
      // Returns a function to get variant options for a base
      return base => (Array.isArray(colorVariants[base]) ? colorVariants[base] : []).map(v => ({ title: v, value: v }));
    },
  },
  methods: {
    updateColor(type, newColor) {
      // Update color for all tables
      const colorToApply = newColor || this.getFullColor(type);
      if (colorToApply) {
        updateColor(type.toLowerCase(), colorToApply);
      }
    },
    getFullColor(type) {
      // Helper to get the full color name for a type (base + variant)
      const base = this.baseColor[type];
      const variant = this.variant[type];
      if (base && variant) return `${base} ${variant}`;
      if (base) return base;
      return '';
    },
    async saveColors() {
      this.isSaving = true
      try {
        // Maybe add validation or API sync here
        console.log('Colors already saved in map')
        this.showConfirmation = true
      } finally {
        this.isSaving = false
      }
    },
    onBaseChange(type) {
      this.variant[type] = '';
      this.updateColor(type);
    },
    variantsForType(type) {
      const base = this.baseColor[type];
      return base ? colorVariants[base] || [] : [];
    },
    swatchColor(type) {
      return resolveVuetifyColor(this.getFullColor(type), this.$vuetify) || '#eee';
    },
    saveTypeColor(type) {
      this.updateColor(type);
    },
    getTextColor(bgColor) {
      const color = getColorValue(bgColor);
      const luminance = (0.299 * color.r + 0.587 * color.g + 0.114 * color.b);
      return (luminance > 186) ? 'black' : 'white';
    },
    // Helper to resolve Vuetify color in template
    resolveVuetifyColor(colorName, vuetify) {
      // Use imported function, fallback to gray
      try {
        return this.$options.methods._resolveVuetifyColor
          ? this.$options.methods._resolveVuetifyColor(colorName, vuetify)
          : require('../utils/themeColorMap').resolveVuetifyColor(colorName, vuetify);
      } catch {
        return '#E0E0E0';
      }
    },
    // Helper: Map Vuetify color name (e.g. 'red lighten-3') to theme key (e.g. 'red-light')
    vuetifyColorNameToThemeKey(colorName) {
      if (!colorName) return '';
      // Handle base
      if (/^[a-z-]+$/.test(colorName)) return colorName + '-base';
      // Handle lighten/darken
      const match = colorName.match(/^([a-z-]+) (lighten|darken)-(\d)$/);
      if (match) {
        const [/*full*/, base, variant, num] = match;
        if (variant === 'lighten') {
          if (num === '5' || num === '4' || num === '3' || num === '2' || num === '1') return base + '-light';
        } else if (variant === 'darken') {
          return base + '-dark';
        }
      }
      // Handle accent
      if (/accent/.test(colorName)) return colorName.replace(/ (accent-\d)/, '-accent');
      return colorName.replace(/ /g, '-');
    },
  },
  watch: {
    // Watcher to handle direct color map changes
    typeColors: {
      handler(newVal) {
        console.log("[DEBUG] ColorSelector typeColors updated:", newVal);
        // Maybe trigger a save or validation here
      },
      deep: true // Ensure nested properties are watched
    }
  }
}
</script>

