// Default fallback colors if nothing is loaded
const defaultColors = {
  'segment': 'light-blue',
  'advert': 'cyan',
  'promo': 'cyan lighten-4',
  'cta': 'cyan lighten-3',
  'trans': 'yellow-accent',
  'unknown': 'grey-light',
  // UI/System colors
  'selection': 'yellow accent-3',     // Selected item highlight
  'hover': 'yellow lighten-4',           // Hover state
  'draglight': 'cyan-light',       // Drag source highlight
  'highlight': 'yellow-light',     // General highlight
  'dropline': 'green-accent',      // Drop indicator
  // Status highlight colors (to be set in color selector UI)
  'draft': 'grey darken-2',           // Draft status
  'approved': 'green-accent',      // Approved status
  'production': 'blue-accent',     // Production status
  'completed': 'yellow-accent',    // Completed status (placeholder)
};

// Export defaultColors for external use
export { defaultColors };

// Always use the static mapping, ignore localStorage
let currentColors = defaultColors;

export const updateColor = (type, newColor) => {
  try {
    console.log("[DEBUG] Updating color:", { type, newColor });
    const updatedColors = { ...currentColors };
    updatedColors[type.toLowerCase()] = newColor;
    currentColors = updatedColors;
    return true;
  } catch (err) {
    console.error("[ERROR] Failed to update color:", err);
    return false;
  }
};

export const getColorValue = (type) => {
  if (!type) {
    console.warn('[DEBUG] getColorValue: type is falsy, returning grey-light');
    return 'grey-light';
  }
  const normalizedType = type.toLowerCase();
  const color =
    currentColors[normalizedType] ||
    currentColors[type] ||
    defaultColors[normalizedType] ||
    defaultColors[type] ||
    'primary'; // fallback to Vuetify's primary if all else fails
  console.log('[DEBUG] getColorValue:', {
    type: normalizedType,
    color: color
  });
  return color;
};

export const getAllColors = () => {
  return { ...currentColors };
};

