// New function to resolve a Vuetify color name to its hex/rgb value
export const resolveVuetifyColor = (colorName, vuetifyInstance) => {
  if (!colorName) return null;

  // First, try to get from Vuetify theme if instance is available
  if (vuetifyInstance && vuetifyInstance.theme) {
    const theme = vuetifyInstance.theme.themes[vuetifyInstance.theme.dark ? 'dark' : 'light'];
    if (theme && theme[colorName]) {
      return theme[colorName];
    }
  }

  // Parse Material Design color syntax (e.g., "light-blue", "cyan lighten-3", "grey darken-2")
  const parts = colorName.trim().split(/\s+/);
  let colorFamily = parts[0];
  let variant = parts.length > 1 ? parts.slice(1).join('-') : 'base';

  // Handle special cases
  if (colorFamily.endsWith('-accent')) {
    // Handle accent colors like "orange-accent" -> "orange"
    colorFamily = colorFamily.replace('-accent', '');
    variant = 'base';
  }
  if (colorFamily.endsWith('-light')) {
    // Handle light colors like "blue-light" -> "blue lighten-1"
    colorFamily = colorFamily.replace('-light', '');
    variant = 'lighten-1';
  }

  // Look up in material design colors
  if (materialDesignColors[colorFamily] && materialDesignColors[colorFamily][variant]) {
    return materialDesignColors[colorFamily][variant];
  }

  // Fallback for common theme colors
  const fallbackColors = {
    'primary': '#1976D2',
    'secondary': '#424242',
    'accent': '#82B1FF',
    'error': '#FF5252',
    'info': '#2196F3',
    'success': '#4CAF50',
    'warning': '#FFC107'
  };

  if (fallbackColors[colorName]) {
    return fallbackColors[colorName];
  }

  console.warn(`[resolveVuetifyColor] Could not resolve color: '${colorName}'`);
  return null;
};